import { css } from '@emotion/react';
import { TextField as MaterialTextField } from '@material-ui/core';
import palette from 'lib/styles/palette';
import React from 'react';

export type TextFieldProps = {
  /**   */
  name?: string;
  type?: 'password' | 'number';
  /**  */
  value?: string;
  /**  */
  variant?: 'outlined' | 'filled' | 'standard';
  width?: string | number;
  /** `true`    */
  maxWidth?: boolean;
  /**   */
  placeholder?: string;
  /**   `true`  */
  error?: boolean;
  /**     */
  helperText?: string;
  /**       */
  onChange?: (e: React.ChangeEvent<HTMLInputElement>) => void;
  InputProps?: any;
  inputProps?: any;
};

/**
 * `TextField`      .
 * undefined      .
 */
function TextField({
  name,
  type,
  value,
  variant = 'standard',
  width,
  maxWidth,
  placeholder,
  error,
  helperText,
  onChange,
  InputProps,
  inputProps,
}: TextFieldProps) {
  return (
    <DefaultTextField
      css={[{ width }, maxWidth && maxWidthStyle]}
      name={name}
      type={type}
      value={value}
      variant={variant}
      placeholder={placeholder}
      error={error}
      helperText={helperText}
      onChange={onChange}
      InputProps={InputProps}
      inputProps={inputProps}
    />
  );
}

const DefaultTextField = (props: TextFieldProps) => (
  <div
    css={css`
      .MuiInput-underline {
        &::before {
          border-bottom: 2px solid ${palette.grey[2]};
        }

        &:hover:not(.Mui-disabled):before {
          border-bottom: 1px solid ${palette.black};
        }

        &::after {
          border-bottom: 2px solid ${palette.grey[7]};
        }
      }

      .MuiOutlinedInput-root {
        .MuiOutlinedInput-input {
          padding: 0.5rem;
        }
        .MuiOutlinedInput-notchedOutline {
          border: 1px solid ${palette.grey[2]};
        }
        &:hover fieldset {
          border: 1px solid ${palette.grey[2]};
        }
      }

      .MuiOutlinedInput-root.Mui-focused {
        .MuiOutlinedInput-notchedOutline {
          border: 1px solid ${palette.grey[4]};
        }
      }

      .MuiOutlinedInput-root.Mui-focused {
        .MuiOutlinedInput-notchedOutline {
          border: 1px solid ${palette.grey[4]};
        }
      }

      .Mui-error {
        &:hover fieldset {
          border: 1px solid ${palette.error[1]};
        }
      }

      .MuiFormHelperText-contained {
        margin: 0.25rem 0;
      }
    `}
  >
    <MaterialTextField {...props} />
  </div>
);

const maxWidthStyle = css`
  min-width: 100% !important;
`;

export default TextField;
